<template>
	<view class="cart">
		<!-- Ëá™ÂÆö‰πâÂØºËà™Ê†è -->
		<view class="nav-bar" :style="{ paddingTop: statusBarHeight + 'px' }">
			<view class="nav-content">
				<text class="nav-title">Ë¥≠Áâ©ËΩ¶</text>
				<text class="nav-edit" @click="toggleEditMode">{{ isEditMode ? 'ÂÆåÊàê' : 'ÁÆ°ÁêÜ' }}</text>
			</view>
		</view>

		<!-- Ë¥≠Áâ©ËΩ¶ÂÜÖÂÆπ -->
		<scroll-view class="cart-content" scroll-y="true">
			<!-- Á©∫Ë¥≠Áâ©ËΩ¶Áä∂ÊÄÅ -->
			<view v-if="cartList.length === 0" class="empty-cart">
				<view class="empty-icon">üõí</view>
				<text class="empty-text">Ë¥≠Áâ©ËΩ¶Á©∫Á©∫Â¶Ç‰πü</text>
				<view class="empty-button" @click="goShopping">ÂéªÈÄõÈÄõ</view>
			</view>

			<!-- Ë¥≠Áâ©ËΩ¶ÂïÜÂìÅÂàóË°® -->
			<view v-else class="cart-list">
				<!-- ÂÖ®ÈÄâ -->
				<view class="select-all">
					<view class="checkbox-item" @click="toggleSelectAll">
						<view class="checkbox" :class="{ checked: allSelected }">
							<text v-if="allSelected" class="check-icon">‚úì</text>
						</view>
						<text class="checkbox-label">ÂÖ®ÈÄâ</text>
					</view>
					<text class="edit-mode" v-if="isEditMode" @click="deleteSelected">Âà†Èô§ÈÄâ‰∏≠</text>
				</view>

				<!-- ÂïÜÂìÅÂàóË°® -->
				<view class="cart-item" v-for="(item, index) in cartList" :key="item.id">
					<view class="checkbox-item" @click="toggleItemSelect(item)">
						<view class="checkbox" :class="{ checked: item.selected }">
							<text v-if="item.selected" class="check-icon">‚úì</text>
						</view>
					</view>

					<view class="item-image-container">
						<up-image 
							:src="item.cover" 
							width="80px" 
							height="80px" 
							radius="8px"
							mode="aspectFill"
						></up-image>
					</view>

					<view class="item-info">
						<text class="item-title">{{ item.title }}</text>
						<text class="item-desc">{{ item.spec || 'ÈªòËÆ§ËßÑÊ†º' }}</text>
						<view class="item-bottom">
							<text class="item-price">Ôø•{{ item.price }}</text>
							<view class="quantity-control">
								<view class="quantity-btn" @click="decreaseQuantity(item)">-</view>
								<view class="quantity-number">{{ item.quantity }}</view>
								<view class="quantity-btn" @click="increaseQuantity(item)">+</view>
							</view>
						</view>
					</view>
				</view>

				<!-- Êé®ËçêÂïÜÂìÅ -->
				<view class="recommend-section">
					<view class="recommend-title">
						<text>Áåú‰Ω†ÂñúÊ¨¢</text>
					</view>
					<view class="recommend-grid">
						<view 
							class="recommend-item" 
							v-for="item in recommendList" 
							:key="item.id"
							@click="addToCart(item)"
						>
							<up-image 
								:src="item.cover" 
								width="100%" 
								height="120px" 
								radius="8px"
								mode="aspectFill"
							></up-image>
							<text class="recommend-item-title">{{ item.title }}</text>
							<view class="recommend-item-bottom">
								<text class="recommend-item-price">Ôø•{{ item.price }}</text>
								<view class="add-btn">+</view>
							</view>
						</view>
					</view>
				</view>
			</view>
		</scroll-view>

		<!-- Â∫ïÈÉ®ÁªìÁÆóÊ†è -->
		<view class="checkout-bar" v-if="cartList.length > 0">
			<view class="checkout-left">
				<view class="checkbox-item" @click="toggleSelectAll">
					<view class="checkbox" :class="{ checked: allSelected }">
						<text v-if="allSelected" class="check-icon">‚úì</text>
					</view>
					<text class="checkbox-label">ÂÖ®ÈÄâ</text>
				</view>
				<view class="total-info">
					<text class="total-label">ÂêàËÆ°Ôºö</text>
					<text class="total-price">Ôø•{{ totalPrice }}</text>
				</view>
			</view>
			<view class="checkout-btn" @click="checkout">
				ÁªìÁÆó({{ selectedCount }})
			</view>
		</view>
	</view>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';

// Áä∂ÊÄÅÊ†èÈ´òÂ∫¶
const statusBarHeight = ref(0);
// ÁºñËæëÊ®°Âºè
const isEditMode = ref(false);

// Ë¥≠Áâ©ËΩ¶Êï∞ÊçÆ
const cartList = ref([
	{
		id: 1,
		title: 'ÁöáÂÆ∂ÂπºÁä¨ÁãóÁ≤Æ premium',
		cover: '/static/goods/ÁÉ≠Èó®(1).png',
		price: 168,
		quantity: 2,
		selected: true,
		spec: '2.5kgË£Ö'
	},
	{
		id: 2,
		title: 'ÂñµÊòü‰∫∫Áå´Á≤Æ plus',
		cover: '/static/goods/ÁÉ≠Èó®(2).png',
		price: 199,
		quantity: 1,
		selected: true,
		spec: '1.8kgË£Ö'
	},
	{
		id: 3,
		title: 'È∏°ËÇâÂπ≤Èõ∂È£ü special',
		cover: '/static/goods/ÁÉ≠Èó®(3).png',
		price: 58,
		quantity: 3,
		selected: false,
		spec: '200gË£Ö'
	},
	{
		id: 4,
		title: 'Ëê•ÂÖªËÜè deluxe',
		cover: '/static/goods/ÁÉ≠Èó®(1).png',
		price: 88,
		quantity: 1,
		selected: false,
		spec: '120gË£Ö'
	}
]);

// Êé®ËçêÂïÜÂìÅ
const recommendList = ref([
	{
		id: 5,
		title: 'ÁâõËÇâÂí¨ËÉ∂',
		cover: '/static/goods/ÁÉ≠Èó®(2).png',
		price: 35
	},
	{
		id: 6,
		title: 'Á£®ÁâôÁé©ÂÖ∑',
		cover: '/static/goods/ÁÉ≠Èó®(3).png',
		price: 38
	},
	{
		id: 7,
		title: 'ÂÆ†Áâ©Ê¢≥Â≠ê',
		cover: '/static/goods/ÁÉ≠Èó®(1).png',
		price: 25
	},
	{
		id: 8,
		title: '‰∫íÂä®ÁêÉ',
		cover: '/static/goods/ÁÉ≠Èó®(2).png',
		price: 45
	}
]);

// ËÆ°ÁÆóÂ±ûÊÄß
const allSelected = computed(() => {
	return cartList.value.length > 0 && cartList.value.every(item => item.selected);
});

const selectedCount = computed(() => {
	return cartList.value.filter(item => item.selected).length;
});

const totalPrice = computed(() => {
	return cartList.value
		.filter(item => item.selected)
		.reduce((total, item) => total + item.price * item.quantity, 0)
		.toFixed(2);
});

// ÊñπÊ≥ï
const toggleEditMode = () => {
	isEditMode.value = !isEditMode.value;
};

const toggleItemSelect = (item) => {
	item.selected = !item.selected;
};

const toggleSelectAll = () => {
	const newSelected = !allSelected.value;
	cartList.value.forEach(item => {
		item.selected = newSelected;
	});
};

const increaseQuantity = (item) => {
	item.quantity++;
};

const decreaseQuantity = (item) => {
	if (item.quantity > 1) {
		item.quantity--;
	}
};

const deleteSelected = () => {
	const selectedItems = cartList.value.filter(item => item.selected);
	if (selectedItems.length === 0) {
		uni.showToast({
			title: 'ËØ∑ÈÄâÊã©Ë¶ÅÂà†Èô§ÁöÑÂïÜÂìÅ',
			icon: 'none'
		});
		return;
	}
	
	uni.showModal({
		title: 'Á°ÆËÆ§Âà†Èô§',
		content: `Á°ÆÂÆöË¶ÅÂà†Èô§ÈÄâ‰∏≠ÁöÑ${selectedItems.length}‰ª∂ÂïÜÂìÅÂêóÔºü`,
		success: (res) => {
			if (res.confirm) {
				cartList.value = cartList.value.filter(item => !item.selected);
				isEditMode.value = false;
				uni.showToast({
					title: 'Âà†Èô§ÊàêÂäü',
					icon: 'success'
				});
			}
		}
	});
};

const addToCart = (item) => {
	const existingItem = cartList.value.find(cartItem => cartItem.title === item.title);
	if (existingItem) {
		existingItem.quantity++;
	} else {
		cartList.value.push({
			...item,
			id: Date.now(),
			quantity: 1,
			selected: true,
			spec: 'ÈªòËÆ§ËßÑÊ†º'
		});
	}
	
	uni.showToast({
		title: 'Â∑≤Ê∑ªÂä†Âà∞Ë¥≠Áâ©ËΩ¶',
		icon: 'success'
	});
};

const checkout = () => {
	if (selectedCount.value === 0) {
		uni.showToast({
			title: 'ËØ∑ÈÄâÊã©Ë¶ÅÁªìÁÆóÁöÑÂïÜÂìÅ',
			icon: 'none'
		});
		return;
	}
	
	uni.showToast({
		title: 'ÁªìÁÆóÂäüËÉΩÂºÄÂèë‰∏≠',
		icon: 'none'
	});
};

const goShopping = () => {
	uni.switchTab({
		url: '/pages/home/index'
	});
};

onMounted(() => {
	// Ëé∑ÂèñÁä∂ÊÄÅÊ†èÈ´òÂ∫¶
	const systemInfo = wx.getSystemInfoSync();
	statusBarHeight.value = systemInfo.statusBarHeight;
});
</script>

